    export default {
        components: {
			videoItem
        },
        data() {
            return {
                isClickChange: false,
                tabIndex: 0,
                newsitems: [123,456],
                tabBars: [{
                    name: '精彩片段',
                    id: 'duanpian'
                }, {
                    name: '文字剧情',
                    id: 'pinglun'
                }],
				showPianduan: true,
				sort: 0
            }
        },
        onLoad: function() {
            // this.newsitems = this.randomfn()
        },
        methods: {
			goBack() {
				uni.navigateBack({
					delta: 1
				});
			},
            changeTab(e) {
                let index = e.target.current;
                if (this.isClickChange) {
                    this.tabIndex = index;
                    this.isClickChange = false;
                    return;
                }
                this.isClickChange = false;
                this.tabIndex = index; //一旦访问data就会出问题
            },
            tapTab(e) { //点击tab-bar
                if (this.tabIndex === e.target.dataset.current) {
                    return false;
                } else {
                    this.isClickChange = true;
                    this.tabIndex = e.target.dataset.current
                }
            },
			goPianduan() {
				if(!this.showPianduan) {
					this.showPianduan = !this.showPianduan
				}
			},
			goFenji() {
				if(this.showPianduan) {
					this.showPianduan = !this.showPianduan
				}
			},
			dianzan(e) {
				console.log(e)
			}
        }
    }



